nav {
  width: 100%;
  height: 3rem;
  position: fixed;
  bottom: 0;
  overflow: hidden;
  z-index: 9999;
}
nav .nav__container {
  width: 100%;
  height: 3rem;
  background-color: rgba(245, 245, 220, 0.534);
  backdrop-filter: blur(15px);
}
nav .nav__container .menu {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}
nav .nav__container .menu .menu-item {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 40px;
  width: 70px;
}
nav .nav__container .menu .menu-item a {
  font-size: 1.3rem;
  color: var(--dark-color);
}